@font-face {
  font-family: "PFDinDisplayPro";
  src: url("../fonts/PFDinDisplayPro-Light.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "PFDinDisplayPro";
  src: url("../fonts/PFDinDisplayPro-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "PFDinDisplayPro";
  src: url("../fonts/PFDinDisplayPro-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

