html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  position: relative;
  margin: 0;
  font-family: "PFDinDisplayPro", sans-serif;
  font-weight: 400;
  background-color: #cecece;
}

img {
  display: block;
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4 {
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1156px;
}

.container-fluid {
  max-width: 1470px;
  margin: 0 auto;
  padding: 0 15px;
  position: relative;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header {
  position: absolute;
  left: 0;
  top: 50px;
  width: 100%;
  z-index: 100;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  display: flex;
  align-items: center;
}

.header__logo span {
  margin-left: 27px;
  font-size: 12px;
  line-height: 14px;
  color: #1d2f39;
  opacity: 0.3;
}

.header__menu {
  display: flex;
}

.header__call {
  display: flex;
  flex-direction: column;
  margin-left: 74px;
}

.header__call-number {
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: #fff;
}

.header__call-link {
  font-weight: 300;
  font-size: 12px;
  line-height: 14px;
  text-decoration: underline;
  color: #ffffff;
}

.menu__list {
  display: flex;
}

.menu__item {
  margin-left: 48px;
}

.menu__link {
  padding-bottom: 7px;
  font-size: 12px;
  line-height: 32px;
  color: #fff;
  position: relative;
}

.menu__link::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 3px;
  background-color: #fff;
  opacity: 0;
  transition: all 0.2s ease;
}

.menu__link:hover::after {
  opacity: 1;
}

.menu__link--active {
  cursor: default;
}

.menu__link--active::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 3px;
  background-color: #fff;
  opacity: 1;
  transition: all 0.2s ease;
}

.section__left {
  flex: 1 1 49%;
}

.section__right {
  flex: 1 1 51%;
}

.section-info {
  padding-top: 220px;
  padding-left: 150px;
  background-color: #fff;
  height: 505px;
  color: #1d2f39;
  font-weight: 600;
}

.section-info__description {
  font-size: 16px;
  line-height: 19px;
  opacity: 0.3;
  margin-bottom: 18px;
}

.section-info__title {
  font-size: 32px;
  line-height: 38px;
  max-width: 385px;
  letter-spacing: 1px;
  font-weight: 700;
}

.section-title {
  background-color: #f0f0f0;
  padding-left: 150px;
  padding-top: 72px;
  min-height: 395px;
  position: relative;
}

.section-title__img {
  position: absolute;
}

.section-title__img.tree {
  bottom: 0;
  right: 0;
}

.section-title__img.rock {
  top: -90px;
  right: 0;
}

.section-title__img.skin {
  right: 0;
  top: -225px;
}

.section-title__main {
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  color: #1d2f39;
  margin-bottom: 8px;
}

.section-title__description {
  font-size: 10px;
  line-height: 12px;
  color: #8c9da7;
  max-width: 180px;
  position: relative;
}

.section-title__description::after {
  content: "";
  width: 24px;
  height: 12px;
  position: absolute;
  bottom: -24px;
  left: 0;
  background-image: url(../../img/arrow-right.svg);
  background-repeat: no-repeat;
  background-size: cover;
}

.section-title--right {
  padding-top: 200px;
  padding-left: 70px;
}

.section-number {
  background-color: #fafafa;
}

.section-number__square {
  width: 192px;
  height: 192px;
  padding: 43px 32px;
}

.section-number__title {
  font-weight: bold;
  font-size: 48px;
  line-height: 58px;
  color: #ffffff;
  opacity: 0.2;
}

.section-number__descr {
  font-size: 8px;
  line-height: 9px;
  color: #ffffff;
  font-weight: 300;
  opacity: 0.8;
}

.section-img {
  min-height: 708px;
}

.section-img img {
  height: 100%;
  object-fit: cover;
}

.square__one {
  background-color: #1d2f39;
}

.square__two {
  background-color: #ddb77f;
  margin-left: auto;
}

.square__tree {
  background-color: #b95955;
}

.square__four {
  background-color: #535a63;
  margin-left: auto;
}

.section-main {
  display: flex;
}
